'use strict';
// port of `productrulesclient`
var engine = require('productrulesengine');

/**
 * Creates a new engine input model.
 * @class
 * @param {any} ruleSet - the ruleSet from the rule service
 */
function EngineInput(ruleSet) {
  if (isNullOrUndefined(ruleSet))
  {
    return;
  }

  this.whiteList = isNonEmptyArray(ruleSet.whiteList) ? ruleSet.whiteList.map(function (x) { return whiteListConversion(x); }) : [];
  this.rules = isNonEmptyArray(ruleSet.rules) ? ruleSet.rules.map(function (x) { return new EngineRule(x); }) : [];
}

/**
 * Runs the rules engine on the given attribute selection
 * @class
 * @param {any} attributeSelection - The attribute selections
 * @returns A validation response
 */
EngineInput.prototype.Validate = function(attributeSelection)
{
  return engine({
    attributeSelection: attributeSelection,
    whiteList: this.whiteList,
    rules: this.rules
  });
};

/**
 * Converts the json from the service format to the engine format
 * @param {object} attribute The object format pulled from the rule service
 * @returns {object} The object format the rule engine expects
 */
function whiteListConversion(attribute) {
  return {
    required: isNullOrUndefined(attribute.required) ? false : attribute.required,
    derived: attribute.derived,
    attributeKey: attribute.attributeKey,
    formula: attribute.formula,
    attributeType: isNullOrUndefined(attribute.type) ? 'listOfValues' : attribute.type,
    attributeValues: isNonEmptyArray(attribute.attributeValues) ? attribute.attributeValues.map(function (x) { return x.value; }) : undefined,
    attributeRanges: isNonEmptyArray(attribute.attributeRanges) ? attribute.attributeRanges : undefined,
  };
}

/**
 * Creates a new engine rule model.
 * @class
 * @param {any} rule - The rule from the service
 */
function EngineRule(rule) {
  this.name = rule.name;
  this.ruleComponents = [];
  if (isNonEmptyArray(rule.predicates)) {
    this.ruleComponents = rule.predicates.map(function (x) { return new RuleComponent(x); });
  }

  if (isNonEmptyArray(rule.results)) {
    this.ruleComponents = this.ruleComponents.concat(rule.results.map(function (x) { return new RuleComponent(x); }));
  }
}

/**
 * Creates a new engine ruleComponent model.
 * @class
 * @param {any} ruleComponent - The ruleComponent from the service
 */
function RuleComponent(ruleComponent) {
  if (!isNullOrUndefined(ruleComponent)) {
    this.componentAttributes = [{
      attribute: ruleComponent.attributeKey,
      values: isNonEmptyArray(ruleComponent.attributeValues) ? ruleComponent.attributeValues.map(function (x) { return x.value; }) : [],
      ranges: ruleComponent.attributeRanges,
      formula: ruleComponent.formula,
      operator: isNullOrUndefined(ruleComponent.operator) ? 'equals' : ruleComponent.operator,
      negate: ruleComponent.negate
    }];
  }
}

/**
 * Helper method to determine if an object is null or undefined
 * @param {any} object - the object to evaluate
 * @returns {Boolean} True if the object is null or undefined, false otherwise
 */
function isNullOrUndefined(object) {
  return object === undefined || object === null;
}

/**
 * Helper method to determine if an object is a non empty array
 * @param {any} object - the object to evaluate
 * @returns {Boolean} True if the object is a non empty array, false otherwise
 */
function isNonEmptyArray(object) {
  return !isNullOrUndefined(object) &&
    Array.isArray(object) &&
    object.length > 0;
}

if (typeof module !== 'undefined') {
  module.exports = {
    EngineInput: EngineInput,
    EngineRule: EngineRule,
    RuleComponent: RuleComponent,
    whiteListConversion: whiteListConversion
  };
}


