'use strict'; 

let util = require('../core/util'); 
let ProductRulesClient = require('./ProductRulesClient'); 
let JCRUtil = require('./JCRUtil'); 

/**
 * @class ProductService
 */
module.exports = class ProductService {

    constructor(param) {
        const init = util.buildInitParam(param); 

        this.LANG = init.lang;
        this.EVENT_PRODUCT_FETCHED = 'product:fetched';
        this.EVENT_PRICING_FETCHED = 'pricing-v3:fetched';

        this._EventBus = init._EventBus;
        
        this._getProductUrl = function(productId) {
            return init.backboneGateway + '/products/products/' + productId + '/normalized'; 
        }; 
        this._getWebsiteJCRUrl = function(pagePath){
            return init.baseUrl + init.restPrefix + 'nodes/v1/website/' + pagePath.replace(/^\//,''); 
        }
        this._getExclusionRulesUrl = function(productTypeId) {
            return init.backboneGateway + '/products/product-types/' + productTypeId + '/exclusion-rules'; 
        };
        this._getTenantDefinitionUrl = function(productTypeId) {
            return init.backboneGateway + '/products/product-types/' + productTypeId + '/tenant-definition'; 
        };

        this._getPricingV3Url = init.backboneGateway + '/pricing/v3/service';

        this._getTaxesUrl = init.backboneGateway + '/tax/v1/drafts';
        
        this._getFileCopyUrl = init.backboneGateway + '/filecopy/v1/fileCopy';
        
        this._getTierDiscountUrl = init.backboneGateway + '/tier/v1/calcTierDiscount';

        this._getArtworkPricingUrl = function(serviceName){
            return 'https://artwork-services-pricing.abc.cimpress.io/v1/' + serviceName;
        }

        this._getRuleValidationsUrl = init.ruleEngineGateway + 'v4/validations';
        
    }
    
    /**
     * Get Product By Id
     * @param {String} productId 
     */
    getProduct(productId) {
        return util.makeBackboneGETRequest(this._getProductUrl(productId)).then(this._dispatchProductIsFetched.bind(this));
    }

    /**
     * Dispatch Product Fetched Event
     * @param {Object} res 
     */
    _dispatchProductIsFetched(res){
        if(res.id){
            this._EventBus.dispatch(this.EVENT_PRODUCT_FETCHED, res);
        }
        return res;
    }

    /**
     * get Page JCR, Magnolia Specific 
     * @param {String} pagePath 
     * @param {Number} depth 
     * @param {Boolean} transform 
     */
    getWebsiteJCR(pagePath, depth, transform){
        return util.makeGETRequest(this._getWebsiteJCRUrl(pagePath), {depth: depth ? depth : 2}, {Accept:'application/json'}).then(function(_data){
            if(transform){
                return JCRUtil.transform(_data);
            }
            return _data;
        });
    }

    /**
     * get Exclusion Rule
     * @param {String} productTypeId 
     */
    getExclusionRules(productTypeId) {
        return util.makeBackboneGETRequest(this._getExclusionRulesUrl(productTypeId));
    }

    /**
     * get Tenant Definition
     * @param {String} productTypeId 
     */
    getTenantDefinition(productTypeId) {
        return util.makeBackboneGETRequest(this._getTenantDefinitionUrl(productTypeId));
    }

    /**
     * find MCP SKU from VariantInfo
     * @param {Object} variantInfo 
     */
    _getVariantSKU(variantInfo){
        let mcpSKUAttribute = variantInfo.attributes.find(a => a.class === 'mcpSKU');
        return (mcpSKUAttribute && mcpSKUAttribute.value && mcpSKUAttribute.value.key) ? mcpSKUAttribute.value.key : null;
    }
    /**
     * Validate User selection by MCP rules services
     * @param {Object} userSelections 
     * @param {Object} variantInfo 
     */
    validateSelections(userSelections, variantInfo){
        // populate mcpSKU, if missing
        if(!userSelections.referenceId){
            if(!variantInfo.attributes){
                return util.sendValidationError(new Error('Invalid variantInfo'));
            }
            let mcpSKU = this._getVariantSKU(variantInfo);
            if(!mcpSKU){
                return util.sendValidationError(new Error('Invalid variantInfo, SKU info not found'));
            }
            userSelections.referenceId = mcpSKU;
        }
        return util.makeMCPRuleGetRequest(this._getRuleValidationsUrl, userSelections);
    }

    /**
     * Once price & quantity is selected by user, make final Validation
     * @param {Object} finalSelections 
     * @param {Object} variantInfo 
     */
    validateFinalSelection(finalSelections, variantInfo){
        let _this = this;

        // searching SKU here to avoid searching of SKU twice
        let mcpSKU = this._getVariantSKU(variantInfo);
        if(!mcpSKU){
            return util.sendValidationError(new Error('Invalid variantInfo, SKU info not found'));
        }
        let userSelections = Object.assign({}, finalSelections);
        userSelections.referenceId = mcpSKU;

        return _this.validateSelections(userSelections).then(result => {
            // When selection is still not complete, try to pass missingAttributes values from `result.remainingOptions`
            if(result.isValid && !result.isComplete){

                (result.missingAttributes || []).map(attribute => {
                    let missingAttribute = result.remainingOptions.find(option => { return option.attributeKey === attribute.attributeKey});
                    if(!userSelections[missingAttribute.attributeKey] && missingAttribute.attributeValues && missingAttribute.attributeValues.length){
                        userSelections[missingAttribute.attributeKey] = missingAttribute.attributeValues[0].value;
                    }
                    else if(!userSelections[missingAttribute.attributeKey] && missingAttribute.attributeRanges && missingAttribute.attributeRanges.length){
                        userSelections[missingAttribute.attributeKey] = missingAttribute.attributeRanges[0].minimum;
                    }
                });

                return _this.validateSelections(userSelections).then(finalResult => {
                    if(!finalResult.isValid){
                        //console.error('ABC:ERROR', 'final selection resulted in InValid selection', 'userSelections: ', JSON.stringify(userSelections), 'result:',JSON.stringify(finalResult));
                        // new selection made it invalid, so need to recursively validate it with `result.violatedRules`
                        let counter = 1, 
                        limit = 5;
                        return _this._validateSelectionWithViolatedRules(finalSelections, userSelections, finalResult, counter, limit);
                    }
                    return finalResult;
                });
            }
            return result;
        });
    }

    /**
     * recursively validate user selection by using data from violatedRules
     * 
     * @param {Object} finalSelections 
     * @param {Object} selections 
     * @param {Object} result 
     * @param {Number} counter 
     * @param {Number} limit 
     */
    _validateSelectionWithViolatedRules(finalSelections, selections, result, counter, limit){
        let _this = this;
        let userSelections = Object.assign({}, selections);

        // Build user selection by picking up data from violated Rules' result
        if(result.violatedRules && result.violatedRules.length){
            for(let i = 0, len = result.violatedRules.length; i < len; i++){
                for(let r = 0, r_len = result.violatedRules[i].results.length; r < r_len; r++){

                    if(finalSelections[result.violatedRules[i].results[r].attributeKey]){
                        console.error('ABC:ERROR', 'violatedRule causing user selection overwrite, rule name:', result.violatedRules[i].name, ' overwrites:', result.violatedRules[i].results[r].attributeKey);
                    }
                    
                    // Use min value for attribute
                    if(result.violatedRules[i].results[r].attributeValues && result.violatedRules[i].results[r].attributeValues.length){
                        userSelections[result.violatedRules[i].results[r].attributeKey] = result.violatedRules[i].results[r].attributeValues[0].value;
                    }
                    else if(result.violatedRules[i].results[r].attributeRanges && result.violatedRules[i].results[r].attributeRanges.length){
                        userSelections[result.violatedRules[i].results[r].attributeKey] = result.violatedRules[i].results[r].attributeRanges[0].minimum;
                    }
                }
            }
        }
        
        // Validate new data and if causes another violation try again
        return _this.validateSelections(userSelections).then((res) => {
            if(!res.isValid && counter < limit){
                counter++;
                return _this._validateSelectionWithViolatedRules(finalSelections, userSelections, res, counter, limit);
            }
            return res;
        });
    }

    /**
     * Validate if User selection are valid or not
     * @param {Object} tenantDefinition 
     * @param {Object} exclusionRules 
     * @param {Array} userSelections 
     * @param {Array} variantInfo 
     * 
     */
    validateSelection(tenantDefinition, exclusionRules, userSelections, variantInfo){
        var _this = this;
        // Build attribute list which are not user selectable but have class 'tenant'
        for (var i = 0, len = variantInfo.attributes.length; i < len; i++) {
            var attribute = variantInfo.attributes[i];
            var attrLabel = attribute['label'][_this.LANG];
            if((attribute['class']=='tenant') && (attribute['selectable']==false) && (userSelections[attrLabel]==undefined)){
                //If class 'tenant', selectable is 'false' and this attribute also does not exist in the 'userSelections' object, then add the first value of such attributes in the user selections object
                userSelections[attrLabel] = attribute['value'][0]['label'][_this.LANG];
            }
        }
        return new Promise(function (resolve, reject) {
            try{
                let ruleSet = {
                    whiteList: tenantDefinition.value.filter(item=>item.class==='tenant'),
                    rules: exclusionRules.value
                };
                const engine = new ProductRulesClient.EngineInput(ruleSet);

                let result = engine.Validate(userSelections);
                resolve(result); 
            }catch(e){
                reject(e);
            }
        });
    }

    /**
     * get pricing from external pricing API, v3
     * 
     * @param {String} productId 
     * @param {Array} userSelections 
     * @param {String} currencyCode 
     * @param {Object} address 
     * @param {Array} quantityList 
     */
    getPricingV3(productId, userSelections, currencyCode, address, quantityList){
        var request = {
            "currency": currencyCode,
            "variableAttributes": userSelections,
            "productId": productId,
            "address": address,
            "requestTime": (new Date()).toISOString()
        };

        if(quantityList){
            request.quantityList = quantityList;
        }
        return util.makeBackbonePostRequest(this._getPricingV3Url, request).then(this._dispatchPricingV3IsFetched.bind(this));
    }

    /**
     * Dispatch Pricing V3 Fetched Event
     * @param {Object} res 
     */
    _dispatchPricingV3IsFetched(res){
        if(res.priceTier){
            this._EventBus.dispatch(this.EVENT_PRICING_FETCHED, res);
        }
        return res;
    }

    /**
     * get product Tax Rate Draft
     * @param {Object} productDetail 
     * @param {Array} userSelections 
     * @param {String} currencyCode 
     * @param {Object} shipToAddress 
     * @param {Object} billToAddress
     */
    getProductTaxRate(productDetail, userSelections, currencyCode, shipToAddress, billToAddress){
        if(!util.typeOf(productDetail, 'object') || !productDetail.productId || !productDetail.quantity){
            return util.sendValidationError(new Error('Invalid productDetail request parameter'));
        }
        productDetail.variableAttributes = userSelections;
        let request = {
            "currency": currencyCode,
            "productDetails": [ productDetail ],
            "shipToAddress": shipToAddress
        }
        if(util.typeOf(billToAddress, 'object')){
            request.billToAddress = billToAddress;
        }
        return util.makeBackbonePostRequest(this._getTaxesUrl, request).then(function(res){
            if(util.typeOf(res, 'array') && res.length){
                // Valid response set
                return (res[0] && res[0].productId ? res[0] : res);
            }
            return res;
        });
    }

    /**
     * get External Tax Rate Drafts for mutiple products
     * @param {Array} productDetails 
     * @param {String} currencyCode 
     * @param {Object} shipToAddress 
     * @param {Object} billToAddress 
     */
    getProductsTaxRate(productDetails, currencyCode, shipToAddress, billToAddress){
        if(!util.typeOf(productDetails, 'array')){
            return util.sendValidationError(new Error('Invalid productDetails request parameter'));
        }
        let request = {
            "currency": currencyCode,
            "productDetails": productDetails,
            "shipToAddress": shipToAddress
        }
        if(util.typeOf(billToAddress, 'object')){
            request.billToAddress = billToAddress;
        }
        return util.makeBackbonePostRequest(this._getTaxesUrl, request);
    }

    /**
     * get File Copy info
     * @param {Boolean} transform 
     */
    getFileCopy(transform){
        return util.makeBackboneGETRequest(this._getFileCopyUrl).then(function(res){
            if(transform && res.minOrderQuantity){
                return {
                    minOrderQuantity: res.minOrderQuantity,
                    quantity: res.quantity,
                    fee: (res.fee && res.fee.number ? res.fee.number * 100 : null),
                    currencyCode: (res.fee && res.fee.currency ? res.fee.currency.currencyCode : null),
                };
            }
            return res;
        });
    }

    /**
     * get Tier Discount
     * @param {String} customerEmailId 
     * @param {String} productId 
     * @param {String} productTypeId 
     */
    getTierDiscount(customerEmailId, productId, productTypeId){
        return util.makeBackbonePostRequest(this._getTierDiscountUrl, {
            "customerEmailId": customerEmailId,
            "productId": productId,
            "productTypeId": productTypeId
        });
    }

    /**
     * get Artwork services
     */
    getArtworkServices(){
        // @todo: for now resulting static data
        return Promise.resolve({
            results: [{
                serviceName: 'JustPrint'
            }]
        });
    }

    /**
     * get Pricing for Artwork Service Name
     * @param {String} serviceName 
     */
    getArtworkServicePricing(serviceName){
        return util.makeGETRequest(this._getArtworkPricingUrl(serviceName));
    }

    /**
     * get Artwork services & pricing
     */
    getArtworkServicesAndPricing(){
        // @todo: for now resulting static data
        var serviceName = 'JustPrint';
        return util.makeGETRequest(this._getArtworkPricingUrl(serviceName)).then(function(res){
            if(util.typeOf(res, 'array') && res.length){
                return { "results": res };
            }
            return res;
        });
    }
}


